.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#quote-tile {
  background-color: white;
  border-radius: 10px;
  color: black;
  padding: 25px;
}
#author {
  text-align: end;
}
#tweet-quote {
color: white;
text-decoration: none;
border-radius: 10px;
padding: 15px;
padding-top: 15px;
}
#new-quote {
  border: none;
  border-radius: 10px;
  text-decoration: none;
  color: white;
  background-color: black;
  cursor: pointer;
  padding: 5px;
  font-size: 15px;
}